import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import {VerticalNavbarComponent} from './feature-component/navigation/vertical-navbar/vertical-navbar.component';
import { HorizontalNavbarComponent } from './feature-component/navigation/horizontal-navbar/horizontal-navbar.component';
import { BgSettingComponent } from './feature-component/bg-setting/bg-setting.component';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {CommonModule} from '@angular/common';
import { LandingPageComponent } from './landing-page/landing-page.component';
import {AppRoutingModule} from './app-routing.module';
import {MatAutocompleteModule, MatDatepickerModule, MatInputModule, MatNativeDateModule} from '@angular/material';
import { SearchOptionComponent } from './feature-component/search-option/search-option.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import {SearchService} from "./service/search/search.service";
import {PreloadStrategyService} from "./preload-strategy.service";

@NgModule({
  declarations: [
    AppComponent,
    VerticalNavbarComponent,
    HorizontalNavbarComponent,
    BgSettingComponent,
    LandingPageComponent,
    SearchOptionComponent,
    PageNotFoundComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,

    AppRoutingModule,

    PerfectScrollbarModule,

    MatDatepickerModule,
    MatNativeDateModule,
    MatInputModule,
    MatAutocompleteModule,
  ],
  providers: [
    PreloadStrategyService,
    SearchService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



