import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {LandingPageComponent} from './landing-page/landing-page.component';
import {PageNotFoundComponent} from './page-not-found/page-not-found.component';
import {PreloadStrategyService} from './preload-strategy.service';

const lazyRoute: Routes = [
  {
    path: 'hotel',
    loadChildren: 'app/modules/lazy-loaded/hotel/hotel.module#HotelModule',
    data: { preload: true }
  },
  {
    path: 'checkout',
    loadChildren: 'app/modules/lazy-loaded/booking-payment/booking-payment.module#BookingPaymentModule',
    data: { preload: true }
  },
  {
    path: 'user',
    loadChildren: 'app/modules/lazy-loaded/user/user.module#UserModule',
    data: { preload: true }
  }
];

const mainRoutes: Routes = [
  ...lazyRoute,
  {
    path: 'home',
    component: LandingPageComponent
  },
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: '**',
    component: PageNotFoundComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      mainRoutes,
      {
        preloadingStrategy: PreloadStrategyService
      }
    )
  ],
  exports: [
    RouterModule
  ]
})

export class AppRoutingModule {}



