import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {IHotel} from '../../../model/main/hotel/hotel';
import {MockHotel} from '../../mock-data/hotel';
import {IPlace} from '../../../model/main/destination';
import {MockPlace} from '../../mock-data/place';
import {selectValueAccessor} from "@angular/forms/src/directives/shared";

@Injectable()
export class SearchService {

  private $placeList: BehaviorSubject<IPlace[]> = new BehaviorSubject(MockPlace);
  private $hotelList: BehaviorSubject<IHotel[]> = new BehaviorSubject(MockHotel);

  constructor() { }

  searchDestination(searchKey: string): Promise<any> {
    let searchResult = this.$placeList.getValue().filter(place => place.city.toLowerCase().includes(searchKey.toLowerCase()));
    if (searchResult && searchResult.length > 0) {
      return Promise.resolve(searchResult);
    } else {
      searchResult = this.$hotelList.getValue().filter(hotel => hotel.name.toLowerCase().includes(searchKey.toLowerCase()));
      return Promise.resolve(searchResult);
    }
  }

}



